    > .titip-content:after {
        @margin:ceil(((@titip-tringle-width / 2) + 2px));
        top:50%;
        margin-top:-@margin
    }
    > .titip-content.thick-border:after {
        @margin:ceil(((@titip-tringle-width / 2) + 5px));
      margin-top:-@margin
    }
}
[class*="titip-left"]{
