.titip-wrapped:after,
.titip-wrapped > .titip-content {
  height: auto;
  width: 170px !important;
  padding: 11px;
  line-height: 19px;
  white-space: normal !important;
  text-align: left;
}
.titip-wrapped-large:after,
.titip-wrapped-large > .titip-content {
  height: auto;
  width: 300px;
  padding: 11px;
  line-height: 19px;
  white-space: normal !important;
